.eventList {
    margin: 2rem auto;
    width: 90%;
    max-width: 40rem;
  }
  
  .eventList ul {
    list-style: none;
    padding: 1rem;
  }
  
  .eventList li {
    border: 1px solid #ccc;
    margin: 0.5rem 0;
    padding: 0.5rem;
    display: flex;
    justify-content: space-between;
  }